import { ChangeDetectionStrategy, Component, Input, OnInit, } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
import { formatDate } from '@angular/common';
//
import { TranslateService } from '@ngx-translate/core';
//
import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';
import { Offer } from '../../models/offer';
import { Release } from '../../../ms-releases/models/releases';
import { Shop } from '../../../ms-shops/models/shops';
import { CURRENCY, Currency } from '../../../ms-shops/models/currency';
import { STATUS, Status } from '../../models/status';
import { SHIPPING, Shipping } from '../../models/shipping';

@Component({
    selector: 'offer-form',
    templateUrl: './offer-form.component.html',
    styleUrls: ['./offer-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class OfferFormComponent extends BaseReactiveFormComponent<Offer> implements OnInit {

    currency: Currency[] = CURRENCY;

    release: FormControl;

    @Input() customized: boolean;

    @Input() releases: Array<Release>;

    @Input() releaseId: string;

    @Input() shops: Array<Shop>;

    status: Array<Status> = STATUS;

    shipping: Array<Shipping> = SHIPPING;

    raffle: boolean;

    displayedColumns: string[] = [
        'checkbox',
        'text',
        'url',
        'actions'
    ];

    links: any[] = [{ text: "lalala", url: "url" }, { text: "lalala", url: "url" }, { text: "lalala", url: "url" }, { text: "lalala", url: "url" }, { text: "lalala", url: "url" }]

    constructor(
        public formBuilder: FormBuilder,
        translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);TODO
    }

    ngOnInit() {
        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.createFormGroup();
    }

    createFormGroup() {
        this.raffle = this.data.raffle;
        this.formGroup = new FormGroup({
            releaseId: new FormControl(this.releaseId?this.releaseId: this.data.releaseId, [Validators.required]),
            shopId: new FormControl(this.data.shopId, [Validators.required]),
            price: new FormControl(this.data.price, [Validators.required]),
            salePercentage: new FormControl(this.data.salePercentage, [Validators.required]),
            status: new FormControl(this.data.status, [Validators.required]),
            shipping: new FormControl(this.data.shipping, [Validators.required]),
            raffle: new FormControl(this.data.raffle.toString(), [Validators.required]),
            currency: new FormControl(this.data.raffle.toString(), [Validators.required]),
            description: new FormControl(this.data.description),
        });

    }

    submitClicked() {
        if (this.formGroup.valid) {
            //this.data.offerDate = new Date("2015/1/12");
            if (this.data.raffle === "true") {
                this.data.raffle = true;               
            } else if (this.data.raffle === "false") {
                this.data.raffle = false;
            }
            this.accept.emit(this.data);
        } else {
            console.log('invalid');
            this.triggerValidation();
        }
    }

}

